import * as posenet from '@tensorflow-models/posenet';
import * as tome from 'chromotome';

import Engine from './utils/engine';
import { initialiseWebcam, getMediaInfo } from './utils/camera';
import { trackMouse } from './utils/utils';
// import Engine from './utils/engine3d';

const WIDTH = 480;
const HEIGHT = 480;
const THRESHOLD = 0.75;
const KEYS = ['nose', 'leftEye', 'rightEye', 'leftEar', 'rightEar', 'leftShoulder', 'rightShoulder'];

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

const cols = 30;
const rows = 30;

const tileSize = WIDTH / cols;

function getReleventPose(poses) {
  const datas = [];

  poses.forEach(poseData => {
    const data = {};

    poseData.keypoints.forEach(p => {
      if (KEYS.includes(p.part) && p.score > THRESHOLD) {
        data[p.part] = p.position;
      }
    })

    datas.push(data);
  });

  return datas;
}

let running = false;
let poseData = [{}, {}, {}];

let palette = tome.get('present-correct');
let colors = [{}, {}, {}];

let mouthType = [0, 0, 0];
let hairType = [0, 0, 0];

const randomMouth = () => Math.floor(Math.random() * 3);
const randomHair = () => Math.floor(Math.random() * 3);

const randomColor = () => {
  return palette.colors[Math.floor(Math.random() * palette.colors.length)];
}

function getPose() {
  if (!running) {
    running = true;

    net.estimateMultiplePoses(getMediaInfo().el, {
      flipHorizontal: true,
      maxDetections: 3,
      scoreThreshold: 0.6,
      nmsRadius: 20
    })
      .then(getReleventPose)
      .then(poses => {
        poses.forEach((data, idx) => {
          const oldData = {...poseData[idx]};

          poseData[idx] = {
            ...poseData[idx],
            ...data
          };

          KEYS.forEach(k => {
            if (!(k in oldData)) {
              colors[idx][k] = randomColor();
            }
          })
        });

        running = false;
      });
  }
}

const floorToIndex = ({ x, y }) => {
  return {
    x: Math.floor(x / tileSize),
    y: Math.floor(y / tileSize)
  };
}

const checkHas = (arr, idx) => {
  for (let i = 0; i < arr.length; i++) {
    console.log(arr, poseData[idx]);
    if (!(arr[i] in poseData[idx])) {
      return false;
    }
  }

  return true;
}

const drawPart = (idx, key) => {
  if (!checkHas([key], idx)) {
    return false;
  }

  const { x, y } = floorToIndex(poseData[idx][key]);

  ctx.fillStyle = colors[idx][key];

  ctx.save();
  ctx.translate(x * tileSize, y * tileSize);
  switch (key) {
    case 'nose':
      ctx.beginPath();
      ctx.moveTo(tileSize / 2, 0);
      ctx.lineTo(tileSize, tileSize * 2);
      ctx.lineTo(0, tileSize * 2);
      ctx.fill();
      break;
    case 'leftEye':
    case 'rightEye':
      ctx.beginPath();
      ctx.arc(tileSize / 2, tileSize / 2, tileSize / 2, 0, Math.PI * 2);
      ctx.fill();
      break;
    case 'leftEar':
      ctx.beginPath();
      ctx.arc(tileSize / 2, tileSize / 2, tileSize, Math.PI * 0.5, Math.PI * 1.5);
      ctx.fill();
      break;
    case 'rightEar':
      ctx.beginPath();
      ctx.arc(tileSize / 2, tileSize / 2, tileSize, Math.PI * 1.5, Math.PI * 2.5);
      ctx.fill();
      break;
    case 'leftShoulder':
    case 'rightShoulder':
      break;
    default:
      ctx.fillRect(0, 0, tileSize, tileSize);
  }

  ctx.restore();
}

const drawBody = (idx) => {
  if (!checkHas(['leftShoulder', 'rightShoulder'], idx)) {
    return false;
  }

  const { x: x1, y: y1 } = floorToIndex(poseData[idx]['leftShoulder']);
  const { x: x2, y: y2 } = floorToIndex(poseData[idx]['rightShoulder']);

  ctx.strokeStyle = colors[idx]['leftShoulder'];
  ctx.beginPath();
  ctx.moveTo(x1 * tileSize, y1 * tileSize);
  ctx.lineTo(x2 * tileSize, y2 * tileSize);
  ctx.stroke();
  ctx.closePath();
}

const drawHair = (idx) => {
  console.log(poseData[idx]);
  if (!checkHas(['rightEye', 'leftEye', 'rightEar', 'leftEar'], idx)) {
    return false;
  }

  const topX = Math.min.apply(this, ['rightEye', 'leftEye']
    .map(k => floorToIndex(poseData[idx][k]).y));

  const leftX = floorToIndex(poseData[idx]['leftEye']).x;
  const rightX = floorToIndex(poseData[idx]['rightEye']).x;

  for (let x = leftX - 1; x <= rightX + 1; x++) {
    ctx.save();
    ctx.translate(x * tileSize, (topX - 3) * tileSize);

    if (hairType[idx] == 0) {
      ctx.beginPath();
      ctx.moveTo(tileSize, 0);
      ctx.lineTo(0, tileSize);
      ctx.stroke();
    }

    if (hairType[idx] == 1) {
      ctx.beginPath();
      ctx.moveTo(tileSize, 0);
      ctx.bezierCurveTo(
        tileSize, tileSize * 0.25,
        tileSize * 0.25, tileSize,
        0, tileSize
      );
      ctx.stroke();
    }

    if (hairType[idx] == 2) {
      ctx.beginPath();
      ctx.moveTo(tileSize * (Math.random() * 2), tileSize * (Math.random() * 2));
      ctx.lineTo(tileSize * (Math.random() * 2), tileSize * (Math.random() * 2));
      ctx.stroke();
    }

    ctx.restore();
  }
}

const drawMouth = (idx) => {
  if (!checkHas(['nose', 'rightEar', 'leftEar'], idx)) {
    return false;
  }

  const bottomX = floorToIndex(poseData[idx]['nose']).y
  const leftX = floorToIndex(poseData[idx]['nose']).x - 2;
  const rightX = floorToIndex(poseData[idx]['nose']).x + 2;

  let len = (rightX - leftX)

  ctx.save();
  ctx.translate((leftX + 1) * tileSize, (bottomX + 4) * tileSize);

  if (mouthType[idx] == 0) {
    for (let x = 0; x < rightX - leftX; x++) {
      ctx.strokeRect(x * tileSize, 0, tileSize / 2, tileSize);
      ctx.strokeRect((x * tileSize) + (tileSize / 2), 0, tileSize / 2, tileSize);
    }
  }

  if (mouthType[idx] == 1) {
    ctx.beginPath();
    ctx.moveTo(0, 0);
    ctx.lineTo((rightX - leftX) * tileSize, 0);
    ctx.stroke();
  }

  if (mouthType[idx] == 2) {
    ctx.beginPath();
    ctx.moveTo(0, 0);
    ctx.bezierCurveTo(
      0, tileSize,
      len * tileSize, tileSize,
      len * tileSize, 0
    );
    ctx.stroke();
  }

  ctx.restore();
}

app.onRender(({ frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);

  getPose();

  if (poseData && Object.keys(poseData[0]).length) {
    KEYS.forEach(k => {
      drawPart(0, k);
    })

    drawBody(0);
    drawHair(0);
    drawMouth(0);
  }
});

trackMouse();

app.canvasEl.addEventListener('click', () => {
  for (let i = 0; i < poseData.length; i++) {
    mouthType[i] = randomMouth();
    hairType[i] = randomHair();

    KEYS.forEach(k => {
      colors[i][k] = randomColor();
    })
  }
})

initialiseWebcam()
  .then(() => {
    console.log('loading model');

    return posenet.load({
      architecture: 'MobileNetV1',
      outputStride: 16,
      inputResolution: 257,
      multiplier: 0.75
    })
  })
  .then(net => {
    window.net = net;

    app.start();
  })
